"use client";
import { useEffect, useState } from "react";
import Link from "next/link";
import supabase from "@/utils/supabase";

export default function ClientesPage() {
  const [clientes, setClientes] = useState([]);

  async function cargar() {
    const { data } = await supabase.from("clientes").select("*").order("id");
    setClientes(data || []);
  }

  async function eliminar(id) {
    await supabase.from("clientes").delete().eq("id", id);
    cargar();
  }

  useEffect(() => { cargar(); }, []);

  return (
    <div className="p-6">
      <h1 className="text-xl font-bold mb-4">Clientes</h1>

      <Link
        href="/admin/clientes/crear"
        className="bg-blue-600 text-white px-3 py-1 rounded"
      >
        + Nuevo Cliente
      </Link>

      <table className="w-full mt-4 border">
        <thead>
          <tr className="bg-gray-200">
            <th>ID</th><th>Nombre</th><th>Tel√©fono</th><th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {clientes.map(c => (
            <tr key={c.id} className="border">
              <td>{c.id}</td>
              <td>{c.nombre}</td>
              <td>{c.telefono}</td>
              <td>
                <Link href={`/admin/clientes/editar/${c.id}`} className="text-blue-600 mr-2">Editar</Link>
                <button className="text-red-600" onClick={() => eliminar(c.id)}>Eliminar</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
